import ROUTES from "../const/routes"

export default function auth (to, from, next, store) {
  //if false => already handled
  //if true => go next, its ok
  //if throw => error, stop propagation
  if (!store.state.authenticated) {
    return next({
      name: ROUTES.WELCOME
    })
  }

  return true;
}



